<template>
  <main>
    <div class="main">
      <div class="wrap">
        <span class="ribbon6"></span>
      </div>
      <h2></h2>
      <div class="line"></div>
      <h3></h3>
      <div id="main" style="width: 400px;height: 260px;"></div>
      <h3></h3>
      <ul>
        <li><p>VC JAVA SQL WEB </p></li>
        <li><p>HTML(5)CSS(3)UI</p></li>
        <li><p>JavaScriptthis jQuery  API ES6ES7 </p></li>
        <li><p> Vue </p></li>
        <li><p> </p></li>
        <li><p>HTTP</p></li>
        <li><p>GitGitHub</p></li>
      </ul>
    </div>
  </main>
</template>

<script>
export default {
  name: 'Skill',
  mounted () {
    var myChart = this.$echarts.init(document.getElementById('main'))
    var option = {
      tooltip: {},
      radar: {
        shape: 'circle',
        name: {
          textStyle: {
            color: '#fff',
            backgroundColor: '#999',
            borderRadius: 3,
            padding: [3, 5]
          }
        },
        indicator: [
          {name: 'JavaScript', max: 100},
          {name: 'JQuery', max: 100},
          // {name: 'React', max: 100},
          {name: '', max: 100},
          {name: 'Vue', max: 100},
          {name: 'HTTP', max: 100},
          {name: '', max: 100},
          {name: 'HTML/CSS', max: 100}
        ]
      },
      series: [
        {
          name: ' vs Budget vs spending',
          type: 'radar',
          data: [
            {
              value: [85, 75, 80, 80, 85, 85, 90],
              name: ''
            }
          ]
        }
      ]
    }
    myChart.setOption(option)
  }
}
</script>

<style scoped>
.main > h2 {
  user-select: none;
}
.main > .line {
  width: 120px;
}
#main {
  align-self: center;
}
ul {
  margin: 0;
}
</style>



